@font-face {
  font-family: "Wonder";
  src: url("/assets/fonts/subset-Wonder-Bold.woff2") format("woff2"), url("/assets/fonts/subset-Wonder-Bold.woff") format("woff");
  font-weight: bold;
  font-style: normal;
  font-display: swap;
}
:root {
  --color-3: #D85416;
  --font-stack-body: "cartograph_cf", "Gill Sans", "Gill Sans MT", Calibri, "Trebuchet MS", sans-serif;
  --font-stack-heading: "Wonder", Georgia, "Times New Roman", Times, serif;
  --color-1: #fff;
  --color-3: #c24c4b;
}

main {
  max-width: 80%;
}
main p {
  max-width: 35em;
  margin-left: auto;
  margin-right: auto;
}

header {
  max-width: 80%;
  margin-left: auto;
  margin-right: auto;
  text-align: center;
}
header p {
  text-transform: uppercase;
  letter-spacing: 0.2em;
}

h1 {
  font-size: 3em;
  text-transform: uppercase;
}